import { useBreakpoint } from "gatsby-plugin-breakpoints"
import * as React from "react"
import styled from "styled-components"

import { COLOR } from "components/color"
import { BREAK_POINT } from "components/sizes"

import { Notes } from "components/molecules/Notes"

import TitlePCImage from "images/common/subsidy-cost-image-title_pc.svg"
import TitleSPImage from "images/common/subsidy-cost-image-title_sp.svg"
import CostImagePCImage from "images/common/subsidy-cost-image_pc.png"
import CostImageSPImage from "images/common/subsidy-cost-image_sp.png"

const TitlePCWidth = "966px"
const TitleSPWidth = "335px"

const Container = styled.div`
  background-color: ${COLOR.white};
  box-shadow: inset 0px -1px 0px ${COLOR.grayGallery};
  padding: 68px 20px;

  @media (max-width: ${BREAK_POINT.w768}px) {
    padding: 58px 20px;
  }
`

const TitleImage = styled.img`
  display: block;
  width: min(${TitlePCWidth}, 100%);
  margin: 0 auto;

  @media (max-width: ${BREAK_POINT.w768}px) {
    width: ${TitleSPWidth};
  }
`

const SimulationTitle = styled.h1`
  width: fit-content;
  color: ${COLOR.main};
  margin: 48px auto 0;
  font-size: 24px;
  line-height: 32px;

  @media (max-width: ${BREAK_POINT.w768}px) {
    margin: 37px auto 0;
    font-weight: 600;
    font-size: 16px;
    line-height: 26px;
  }
`

const SimulationDescription = styled.p`
  width: fit-content;
  margin: 8px auto 0;
  color: ${COLOR.main};
  font-size: 14px;
  line-height: 24px;
  text-align: center;
`

const SimulationLine = styled.hr`
  width: 666px;
  margin: 24px auto 0;
  size: 2px;
  color: ${COLOR.grayGallery};

  @media (max-width: ${BREAK_POINT.w768}px) {
    display: none;
  }
`
const CostImage = styled.img`
  display: block;
  width: 690px;
  margin: 24px auto 0;

  @media (max-width: ${BREAK_POINT.w768}px) {
    width: 270px;
    margin: 37px auto 0;
  }
`

const StyledNotes = styled(Notes)`
  width: min(${TitlePCWidth}, 100%);
  margin: 42px auto 0;

  @media (max-width: ${BREAK_POINT.w768}px) {
    width: ${TitleSPWidth};
    margin: 37px auto 0;
  }
`

interface SubsidySectionCostImageProps {
  readonly className?: string
}

export const SubsidySectionCostImage: React.VFC<SubsidySectionCostImageProps> = props => {
  const { className } = props
  const breakpoints = useBreakpoint()

  return (
    <Container className={className}>
      {breakpoints.sp ? (
        <TitleImage src={TitleSPImage} alt="21/4" />
      ) : (
        <TitleImage src={TitlePCImage} alt="21/4" />
      )}

      <SimulationTitle></SimulationTitle>
      <SimulationDescription>
        ITAir2
      </SimulationDescription>
      <SimulationLine />
      {breakpoints.sp ? (
        <CostImage src={CostImageSPImage} alt="Air" />
      ) : (
        <CostImage src={CostImagePCImage} alt="Air" />
      )}
      <StyledNotes
        className="notes"
        notes={[
          "",
          "ITAir2",
        ]}
      />
    </Container>
  )
}
