import { withPrefix } from "gatsby-link"
import { useBreakpoint } from "gatsby-plugin-breakpoints"
import * as React from "react"
import styled from "styled-components"

import { COLOR } from "components/color"
import { BREAK_POINT } from "components/sizes"

import { ExternalLinkButton } from "components/atoms/ExternalLinkButton"
import { SvgRightArrowIcon } from "components/atoms/Icon/RightArrowIcon"
import { LinkWithNewTabIcon } from "components/molecules/LinkWithNewTabIcon"
import { Notes } from "components/molecules/Notes"

import { PageName } from "functions/adobeAnalytics/common"
import { createURLWithParameters, Parameter } from "functions/url"

const Container = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: inset 0px -1px ${COLOR.grayGallery}, inset 0 1px ${COLOR.grayGallery};

  @media (max-width: ${BREAK_POINT.w768}px) {
    box-shadow: inset 0px -1px ${COLOR.grayGallery};
  }
`

const RowContainer = styled.div`
  display: flex;
  flex-direction: row;

  @media (max-width: ${BREAK_POINT.w768}px) {
    flex-direction: column;
  }
`

const ColumnContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 400px;

  & + & {
    width: 380px;
    margin-left: 32px;
  }

  @media (max-width: ${BREAK_POINT.w768}px) {
    width: 335px;

    & + & {
      width: 335px;
      margin-left: 0;
    }
  }
`

const ButtonTitle = styled.span`
  font-weight: bold;
  font-size: 14px;
  line-height: 24px;
  color: ${COLOR.black60};
`

const ButtonTitleSub = styled.span`
  font-size: 14px;
  line-height: 20px;
  color: ${COLOR.black60};
`

const ButtonTitleRow = styled.div`
  display: flex;
  align-items: flex-end;

  @media (max-width: ${BREAK_POINT.w768}px) {
    flex-direction: column;
    align-items: center;
    // margin-top: 20px; IT
  }
`

const Line = styled.hr`
  width: 100%;
  size: 1px;
  color: ${COLOR.grayGallery};
  margin-top: 20px;
`

const LinkButton = styled.a`
  display: flex;
  align-items: center;
  width: 100%;
  border-radius: 6px;
  padding: 26px 16px;
  color: ${COLOR.main};
  font-size: 20px;
  line-height: 20px;
  margin-top: 12px;
  border: 1px solid ${COLOR.main};
  background-color: ${COLOR.white};
  box-shadow: inset 0px -3px 0px ${COLOR.main};

  &:hover {
    opacity: 0.7;
  }

  &:active {
    box-shadow: 0 0;
    transform: translate(0, 2px);
  }
`

const LinkSpan = styled.span`
  width: 100%;
  text-align: center;
`

const ConfirmationOfTerms = styled.div`
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: 16px;
`

const TextBeforeExternalLink = styled.span`
  font-size: 12px;
  line-height: 14px;
  margin-right: 4px;
  color: ${COLOR.black60};
`

const TextAfterExternalLink = styled.span`
  font-size: 12px;
  line-height: 14px;
  margin-left: 4px;
  color: ${COLOR.black60};
`

const StyledExternalLinkButton = styled(ExternalLinkButton)`
  width: max(380px, 100%);
  margin-top: 12px;
  font-size: 20px;
  line-height: 26px;

  @media (max-width: ${BREAK_POINT.w768}px) {
    width: 100%;
    line-height: 20px;
  }
`

const StyledNotes = styled(Notes)`
  line-height: 14px;
  margin-top: 32px;
`

interface PaidVersionApplicationProps {
  readonly className?: string
  readonly pageName: PageName
  readonly clientId: string
  readonly sectionNumber: number
  readonly termsOnClick: () => void
  readonly navigationButtonOnClick: () => void
  readonly paidVersionApplicationOnClick: () => void
}

export const PaidVersionApplication: React.VFC<PaidVersionApplicationProps> = props => {
  const breakpoints = useBreakpoint()
  const TermOfUseParameter: Parameter[] = [
    {
      key: "ref",
      value: props.pageName + "_termsofuse" + String(props.sectionNumber),
    },
  ]

  const plfBaseUrl = process.env.GATSBY_AIR_PLF_URL + "apply/"
  const parameters: Parameter[] = [
    {
      key: "goodsCd",
      value: "31247",
    },
    {
      key: "planCd",
      value: "N001",
    },
    {
      key: "serviceCd",
      value: "00066",
    },
    {
      key: "client_id",
      value: props.clientId,
    },
    {
      key: "route",
      value: "c",
    },
  ]

  return (
    <Container className={props.className}>
      <RowContainer>
        {/* <ColumnContainer>
          <ButtonTitle>IT</ButtonTitle>
          <LinkButton className="link-button" href={withPrefix("/it-hojo/")} onClick={props.navigationButtonOnClick}>
            <LinkSpan>IT</LinkSpan>
            <SvgRightArrowIcon size={18} />
          </LinkButton>
          {breakpoints.sp && <Line />}
        </ColumnContainer> */}
        <ColumnContainer>
          <ButtonTitleRow>
            <ButtonTitle></ButtonTitle>
            {/* <ButtonTitleSub>IT</ButtonTitleSub> */}
          </ButtonTitleRow>
          <StyledExternalLinkButton
            className="external-link-button"
            link={createURLWithParameters(plfBaseUrl, parameters)}
            onClick={props.paidVersionApplicationOnClick}
          >
            
          </StyledExternalLinkButton>
          <ConfirmationOfTerms>
            <TextBeforeExternalLink></TextBeforeExternalLink>
            <LinkWithNewTabIcon
              text=""
              link={createURLWithParameters("/invoice/terms/", TermOfUseParameter)}
              onClick={props.termsOnClick}
            />
            <TextAfterExternalLink></TextAfterExternalLink>
          </ConfirmationOfTerms>
        </ColumnContainer>
      </RowContainer>
      {/* <StyledNotes
        className="notes"
        notes={[
          "ITAir2",
          "IT",
        ]}
        marginBetweenNotes="8px"
      /> */}
    </Container>
  )
}
