import { useBreakpoint } from "gatsby-plugin-breakpoints"
import * as React from "react"
import styled, { css } from "styled-components"

import { COLOR } from "components/color"
import { BREAK_POINT } from "components/sizes"

import { BorderedText } from "components/atoms/BorderedText"
import { SectionTitleH4 } from "components/atoms/SectionTitleH4"
import { Notes } from "components/molecules/Notes"

import SpecialOfferImage from "images/apply/special-offer.svg"

const Container = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
`

const Explanation = styled.div`
  text-align: center;
  margin-top: 48px;
`

const StyleExplanation = css`
  font-size: 24px;
  line-height: 32px;
  color: ${COLOR.black87};
  @media (max-width: ${BREAK_POINT.w768}px) {
    font-size: 16px;
    line-height: 26px;
  }
`

const ConditionText = styled.p`
  ${StyleExplanation}
`

const SpecialOfferText = styled.p`
  ${StyleExplanation}
  font-weight: bold;
`

const FurtherSpecialOfferSpan = styled.span`
  color: ${COLOR.orangeClementine};
`

const StyledBorderedText = styled(BorderedText)`
  width: 480px;
  margin: 24px auto 0;
  line-height: 18px;
  @media (max-width: ${BREAK_POINT.w768}px) {
    width: 100%;
  }
`

const StyledNotes_Example = styled(Notes)`
  margin-top: 24px;
`

const ImageContainer = styled.div`
  margin-top: 24px;
  text-align: center;
`

const StyledNotes = styled(Notes)`
  margin-top: 8px;
  text-align: center;
`

const Img = styled.img`
  width: 100%;
  height: auto;
`

interface SpecialOfferProps {
  readonly className?: string
}

export const SpecialOffer: React.VFC<SpecialOfferProps> = props => {
  const breakpoint = useBreakpoint()
  const noteBorderText = ""
  const noteBorderTextSP = "\n"
  return (
    <Container className={props.className}>
      <SectionTitleH4></SectionTitleH4>
      <Explanation>
        <ConditionText></ConditionText>
        <SpecialOfferText>
          2
          <br />
          <FurtherSpecialOfferSpan>1</FurtherSpecialOfferSpan>
        </SpecialOfferText>
        <StyledBorderedText>{breakpoint.sp ? noteBorderTextSP : noteBorderText}</StyledBorderedText>
      </Explanation>
      <StyledNotes_Example
        className="notes"
        notes={[
          "513Air56",
          "6307",
          "8",
        ]}
        marginBetweenNotes="8px"
      />
      <ImageContainer>
        <Img src={SpecialOfferImage} width={818} height={278} alt="" />
        {breakpoint.sp && <StyledNotes className="notes" notes={[""]} />}
      </ImageContainer>
    </Container>
  )
}
